:root{
  --colorPrincipal: #4392F1;
  --colorSecundario: #FDCA40;
  --peligro: #E3170A;
}

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

/* Estilos globales */
h2, h3, h4, p, a, label, input, textarea{
  font-family: 'Yanone Kaffeesatz', sans-serif;
  font-size: 34px;
  color: #444444;
}
h1{
  font-family: 'Lobster', cursive;
  color: var(--colorPrincipal);
}
h2{
  font-size: 35px;
  margin: 20px 0 0 0;
  text-align: center;
  font-weight: bold;
}
p, a, label, input, textarea{
  font-size: 18px;
}
a{
  text-decoration: none;
}
i{
  font-size: 25px;
}
.btn{
  color: var(--colorSecundario);
  border: 2px solid var(--colorSecundario);
  background-color: transparent;
  padding: 8px 20px;
  border-radius: 8px;
  font-weight: bold;

  outline: none;
  transition: all 0.3s ease-in-out;
}
.btn:hover{
  cursor: pointer;
  color: #ffffff;
  border: 2px solid var(--colorSecundario);
  background-color: var(--colorSecundario);
}
body{
  background-color: #F2F2F2;
}
.carta{
  margin: 5px auto;
  background-color: #ffffff;
  padding: 15px;
  text-align: center;
  width: 95%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;

  -webkit-box-shadow: 0px 5px 10px -5px rgba(0,0,0,0.23);
  -moz-box-shadow: 0px 5px 10px -5px rgba(0,0,0,0.23);
  box-shadow: 0px 5px 10px -5px rgba(0,0,0,0.23);
}
@media (min-width: 768px){
    .carta{
      width: 100%;
    }
}
article{
  display: flex;
  flex-flow: column;
  justify-content: center;
}
article p{
  flex: 1;
}
article img,
article a{
  margin: 0 auto;
}
@media (min-width: 768px){
    .carta{
      width: 45%;
      margin: 10px;
      border-radius: 10px;
    }
    article{
      flex-flow: row wrap;
    }
}
.error{
  background-color: var(--peligro);
  color: #ffffff;
  padding: 10px;

  -webkit-box-shadow: 0px 5px 10px -5px rgba(0,0,0,0.23);
  -moz-box-shadow: 0px 5px 10px -5px rgba(0,0,0,0.23);
  box-shadow: 0px 5px 10px -5px rgba(0,0,0,0.23);
}


/* Header */
header{
  text-align: center;
  margin: 10px 0;
}
header nav ul{
  display: flex;
  list-style: none;
  margin: 20px 0;
  padding: 0;
  justify-content: space-evenly;
}
header nav ul li{
  font-size: 25px;
}
header .foto{
  margin: 0 auto;
  position: relative;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  overflow: hidden;
}
header h2 span{
  display: block;
}
.foto img{
  position: absolute;
  height: 100%;
  left: -50px;
}

/* iconos */
.fiverr{
  color: #1DBF73;
}
.fa-linkedin-in{
  color: #0077B5;
}
.fa-instagram{
  color: #CE2589;
}
.fa-twitter{
  color: #1DA1F2;
}
.fa-facebook-f{
  color: #4267B2;
}
.fa-php{
  color: #8892BF;
}
.fa-laravel{
  color: #F4645F;
}
.fa-react{
  color: #61DAFB;
}

/* Proyectos */
.logo-proyectos{
  max-width: 40%;
}

/* Contacto */
.input-field{
  text-align: left;
  margin: 10px 0;
}
.form-control{
  width: 100%;
  min-width: 100%;
  max-width: 100%;
  border: 1px solid #d2d2d2;

  padding: 5px;
  margin: 5px 0;
}
.input-btn{
  text-align: right;
}